import React from 'react'
import { Button } from 'antd'

export default () => {
  return (
    <div className="sub-icon-bar">
      <Button
        icon="file"
        shape="circle"
      />
      <Button
        icon="file-add"
        shape="circle"
      />
      <Button
        icon="copy"
        shape="circle"
      />
      <Button
        icon="folder-open"
        shape="circle"
      />
      <Button
        icon="save"
        shape="circle"
      />
      <Button
        icon="edit"
        shape="circle"
      />

    </div>
  )
}


