import React, { Component } from 'react'

class View extends Component {
  render() {
    return (
      <div className="user-info">
        <div className="user">
          <i className="user-icon"/>
          SKytale
        </div>
        <button className="logout"/>
      </div>
    );
  }
}

export default View;


