export default {
  "global": {
    "splitterSize": 8,
    "enableEdgeDock": false,
    "tabSetEnableMaximize": true,
    "tabEnableClose": true,
    "tabSetEnableTabStrip": true,
    "tabSetHeaderHeight": 30,
    "tabSetTabStripHeight": 30,
    "borderBarSize": 20
  },
  "layout": {
    "type": "row",
    "weight": 100,
    "children": [{
      "type": "tabset",
      "weight": 25,
      "enableDrop": false,
      "enableDrag": false,
      "enableClose": true,
      "enableDivide": false,
      "selected": 0,
      "children": [{
        "type": "tab",
        "enableDrag": false,
        "enableClose": false,
        "name": "Subject",
        "component": "subject"
      },{
        "type": "tab",
        "enableDrag": false,
        "enableClose": false,
        "name": "Palette",
        "component": "palette"
      },{
        "type": "tab",
        "enableDrag": false,
        "enableClose": false,
        "name": "Data",
        "component": "tree"
      }]
    }, {
      "type": "row",
      "weight": 75,
      "children": [{
        "type": "tabset",
        "enableClose": false,
        "selected": 0,
        "children": [{
          "type": "tab",
          "name": "Model",
          "enableDrag": false,
          "component": "canvas",
          "id": "work-model",
          "className": "workflow--model"
        },{
          "type": "tab",
          "name": "Share Model1",
          "enableDrag": false,
          "component": "canvas",
          "id": "work-share1",
          "className": "workflow--share"
        },{
          "type": "tab",
          "name": "Share Model2",
          "enableDrag": false,
          "component": "canvas",
          "id": "work-share2",
          "className": "workflow--share"
        }]
      }]
    }]
  },
  "borders": [
    {
      "type": "border",
      "location": "right",
      "weight": 20,
      "enableDeleteWhenEmpty": true,
      "children": [
        {
          "type": "tab",
          "name": "Property",
          "enableDrag": false,
          "enableClose": false,
          //"component": "property"
        }
      ]
    },
    {
      "type": "border",
      "location": "bottom",
      "enableDeleteWhenEmpty": true,
      "children": [
        {
          "type": "tab",
          "name": "Table1",
          "component": "table",
          "id": "#24"
        },
        {
          "type": "tab",
          "name": "Table2",
          "component": "table",
          "id": "#25"
        }
      ]
    }
  ]
}


