import React, { Component } from 'react'
import { Menu } from 'antd';

const SubMenu = Menu.SubMenu;
const Divider = Menu.Divider;

class View extends Component {
  handleClick = (e) => {
    console.log('click ', e);
  }

  render() {
    return (
      <Menu
        onClick={this.handleClick}
        mode="horizontal"
      >
        <SubMenu title={<span>File</span>}>
          <Menu.Item key="file:newSubject">New Subject</Menu.Item>
          <Menu.Item key="file:newModel">New Model</Menu.Item>
          <Menu.Item key="file:open">Open</Menu.Item>
          <Menu.Item key="file:save">Save</Menu.Item>
          <Menu.Item key="file:saveAs">Save As</Menu.Item>
          <Menu.Item key="file:delete">Delete</Menu.Item>
          <Divider />
          <Menu.Item key="file:copy">Copy</Menu.Item>
          <Menu.Item key="file:move">Move</Menu.Item>
          <Menu.Item key="file:rename">Rename</Menu.Item>
          <Divider />
          <Menu.Item key="file:share">Share</Menu.Item>
          <Divider />
          <Menu.Item key="file:property">Property</Menu.Item>
        </SubMenu>
        <SubMenu title={<span>Edit</span>}>
          <Menu.Item key="edit:copy">Copy</Menu.Item>
          <Menu.Item key="edit:cut">Cut</Menu.Item>
          <Menu.Item key="edit:paste">Paste</Menu.Item>
          <Divider />
          <Menu.Item key="edit:delete">Delete</Menu.Item>
          <Menu.Item key="edit:deleteAll">Delete All</Menu.Item>
          <Menu.Item key="edit:rename">Rename</Menu.Item>
          <Divider />
          <Menu.Item key="edit:property">Property</Menu.Item>
        </SubMenu>
        <SubMenu title={<span>View</span>}>
          <Menu.Item key="view:arrange">Arrange</Menu.Item>
          <Menu.Item key="view:zoom">Zoom</Menu.Item>
          <Divider />
          <Menu.Item key="view:property">Property</Menu.Item>
        </SubMenu>
        <SubMenu title={<span>Canvas</span>}>
          <Menu.Item key="canvas:workflow">Workflow</Menu.Item>
          <Menu.Item key="canvas:report">Report</Menu.Item>
          <Menu.Item key="canvas:jobMonitoring">Job Monitoring</Menu.Item>
        </SubMenu>
        <SubMenu title={<span>Run</span>}>
          <Menu.Item key="run:validate">Validate</Menu.Item>
          <Divider />
          <Menu.Item key="run:runAll">Run all</Menu.Item>
          <Menu.Item key="run:runSelected">Run selected</Menu.Item>
        </SubMenu>
        <SubMenu title={<span>Deploy</span>}>
          <Menu.Item key="deploy:publish">Publish</Menu.Item>
          <Menu.Item key="deploy:restore">Restore</Menu.Item>
        </SubMenu>
        <SubMenu title={<span>Control</span>}>
          <Menu.Item key="control:palette">Palette</Menu.Item>
          <Menu.Item key="control:preference">Preference</Menu.Item>
          <Menu.Item key="control:security">Security</Menu.Item>
          <Menu.Item key="control:schedule">Registration Schedule</Menu.Item>
        </SubMenu>
        <SubMenu title={<span>Community</span>}>
          <Menu.Item key="community:learn">Learn</Menu.Item>
          <Menu.Item key="community:guide">Guide</Menu.Item>
          <Menu.Item key="community:metadata">Metadata</Menu.Item>
          <Menu.Item key="community:discuss" disabled={true}>Discuss</Menu.Item>
          <Menu.Item key="community:propose"disabled={true}>Propose</Menu.Item>
        </SubMenu>
        <Menu.Item key="pdm">
          <a href="https://naver.com" target="_blank" rel="noopener noreferrer">PDM</a>
        </Menu.Item>
      </Menu>
    );
  }
}


export default View;


