import React, { Component } from 'react'
import { Button } from 'antd'
import IconItem from '@/component/IconItem'

const collection = [{
  "name": "DT Class.",
  "prvt_proxy_id": "74",
  "class_name": "DecisionTreeClassifier",
  "division_type": "SP"
},{
  "name":"RDB-Import",
  "prvt_proxy_id":"17",
  "class_name":"BlendRdb2HiveSqoop",
  "division_type":"DP"
},{
  "name":"PCA",
  "prvt_proxy_id":"97",
  "class_name":"PcaClustering",
  "division_type":"SP"
},{
  "name":"Correlation",
  "prvt_proxy_id":"63",
  "class_name":"Correlation",
  "division_type":"SP"
},{
  "name":"Select",
  "prvt_proxy_id":"31",
  "class_name":"SelectHive",
  "division_type":"DP"
},{
  "name":"Join",
  "prvt_proxy_id":"32",
  "class_name":"JoinHive",
  "division_type":"DP"
},{
  "name":"RF Class.",
  "prvt_proxy_id":"76",
  "class_name":"RandomForestClassifier",
  "division_type":"SP"
},{
  "name":"SVM Class.",
  "prvt_proxy_id":"77",
  "class_name":"SvmClassifier",
  "division_type":"SP"
},{
  "name":"Filter",
  "prvt_proxy_id":"36",
  "class_name":"FilterHive",
  "division_type":"DP"
},{
  "name":"Sort",
  "prvt_proxy_id":"37",
  "class_name":"SortHive",
  "division_type":"DP"
}]


class MainToolbar extends Component {
  state = {
    foldIcon: 'menu-fold'
  }

  menuToggle = (e) => {
    const foldIcon = this.state.foldIcon === 'menu-fold' ?
      'menu-unfold' : 'menu-fold'
    this.setState({ foldIcon })
  }

  render() {
    const dpList = collection
      .filter(({ division_type }) => division_type === 'DP')
      .map(
        ({name, class_name, prvt_proxy_id, division_type}) => (
          <IconItem
            key={prvt_proxy_id}
            id={prvt_proxy_id}
            name={name}
            class_name={class_name}
            type={division_type}
          />
        )
      );
    const spList = collection
      .filter(({ division_type }) => division_type === 'SP')
      .map(
        ({name, class_name, prvt_proxy_id, division_type}) => (
          <IconItem
            key={prvt_proxy_id}
            id={prvt_proxy_id}
            name={name}
            class_name={class_name}
            type={division_type}
          />
        )
      );

    return (
      <div className="main-bar">
        <Button
          type="primary"
          className="btn-menu-fold"
          icon={this.state.foldIcon}
          size="large"
          onClick={this.menuToggle}
        />

        <div className="icon-tool-bar">
          { dpList }
        </div>
        <div className="icon-tool-bar">
          { spList }
        </div>
      </div>
    );
  }
}


export default MainToolbar;


