import React from 'react'

export default ({name, class_name, id, type}) => {
  const paletteName = `palette-${type}`
  const iconName = `icon-${class_name.toLowerCase()}`
  return (
    <span
      className={paletteName}
    >
      <i
        key={id}
        className={iconName}
        title={name}
      />
    </span>
  )
}


