import React, { Component } from 'react'
import async from '@/utils/async'
import FlexLayout from 'flexlayout-react'
import LayoutJson from './config.layout'

// Async Component
const Subject = async(() => import('@/component/Subject'))
const Palette = async(() => import('@/component/Palette'))

const Grid = async(() => import('@/component/Grid'))
const Gojs = async(() => import('@/component/Gojs'))

class View extends Component {
  constructor(props) {
    super(props);
    this.state = {
      model: FlexLayout.Model.fromJson(LayoutJson)
    };
  }

  factory(node) {
    const component = node.getComponent();
    if (component === "subject") {
      return <Subject />;
    } else if (component === "palette") {
      //const name = node.getName();
      return <Palette />;
    } else if (component === "table") {
      //const name = node.getName();
      return <Grid />;
    } else if (component === "canvas") {
      let rect = node.getRect()
      return <Gojs
        id={node.getId()}
        width={rect.width}
        height={rect.height}
      />;
    }else{
      return <div>blabla</div>
    }



    /*
     if (component === "subject") {
     return <Subject />;
     } else if (component === "palette") {
     return <Palette/>;
     } else if (component === "canvas") {
     let rect = node.getRect()
     return <GoDiagram
     width={rect.width}
     height={rect.height}
     />;
     } else if (component === "tree") {
     return <Tree/>;
     //    } else if (component === "property") {
     //      return <Forms/>;
     } else if (component === "table") {
     const name = node.getName();
     return <Grid data={data[name]}/>;
     } else if (component === "text") {
     return <div>blabla</div>;
     }
     */
  }

  render() {
    return (
      <div className="outer-layout">
        <div className="inner-layout">
          <FlexLayout.Layout ref="layout"
                             model={this.state.model}
                             factory={this.factory.bind(this)}/>
        </div>
      </div>
    );
  }
}

export default View;


